}

.m-guide .m-arr-top {
    border-color: transparent transparent rgba(255,130,0,.9) transparent;
    top: 0;
    left: 1.25rem;
    -webkit-transform: translate(0,-100%);
    transform: translate(0,-100%)
}

.m-guide .m-arr-bottom {
    border-color: rgba(255,130,0,.9) transparent transparent transparent;
    bottom: 0;
    left: 1.25rem;
    -webkit-transform: translate(0,100%);
    transform: translate(0,100%)
}

.m-animated {
    -webkit-animation-duration: .5s;
    animation-duration: .5s;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both
}

.m-an-pulse {
    -webkit-animation-name: pulse;
    animation-name: pulse;
    -webkit-animation-timing-function: cubic-bezier(.88,.48,.63,1);
    animation-timing-function: cubic-bezier(.88,.48,.63,1)
}

@-webkit-keyframes pulse {
    0% {
        -webkit-transform: scale(1);
        transform: scale(1)
    }

    50% {
        -webkit-transform: scale(1.5);
        transform: scale(1.5)
    }

    100% {
        -webkit-transform: scale(1);
        transform: scale(1)
    }
}

@keyframes pulse {
    0% {
        -webkit-transform: scale(1);
        transform: scale(1)
    }

    50% {
        -webkit-transform: scale(1.5);
        transform: scale(1.5)
    }

    100% {
        -webkit-transform: scale(1);
        transform: scale(1)
    }
}

